<template>
    <div class="school-search-wrapper">
        <div class="banner"></div>
        <div class="school-search-main">
            <div class="breadcrumd-wrapper">
                <span class="l-title">您的位置：</span>
                <v-breadcrumb separator=">">
                    <v-breadcrumb-item>
                        <router-link to="/">首页</router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>
                        <router-link to="/school-search">名校查询</router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>名校对比</v-breadcrumb-item>
                </v-breadcrumb>
            </div>
            <div class="compare-table">
                <ul class="compare-columns title-column">
                    <li class="compare-rows operate-row">操作</li>
                    <li class="compare-rows logo-row">学校</li>
                    <li class="compare-rows">学龄段</li>
                    <li class="compare-rows">学校类型</li>
                    <li class="compare-rows">就读形式</li>
                    <li class="compare-rows">建校时间</li>
                    <li class="compare-rows">学生年龄</li>
                    <li class="compare-rows">学费</li>
                    <li class="compare-rows">最新学术排名</li>
                    <li class="compare-rows">师生比</li>
                    <li class="compare-rows">学生人数</li>
                    <li class="compare-rows">国际学生比例</li>
                    <li class="compare-rows">所在地区</li>
                    <li class="compare-rows">学校面积</li>
                    <li class="compare-rows">官网地址</li>
                </ul>
                <ul class="compare-columns" v-for="(sl,index) in schoolList" :key="index">
                    <li class="compare-rows operate-row">
                        <button class="cancel-btn" @click="cancelCompare(sl)">取消对比</button>
                    </li>
                    <li class="compare-rows logo-row">
                        <div class="logo-show">
                            <img :src="'http://47.52.167.136:1328' + sl.logo" alt="">
                        </div>
                        <span class="name-panel">{{sl.schoolcn}}</span>
                        <span class="name-panel">{{sl.schoolen}}</span>
                    </li>
                    <li class="compare-rows">{{sl.startage+'岁-'+sl.endage+'岁'}}</li>
                    <li class="compare-rows">{{sl.schooltypename?sl.schooltypename:'——'}}</li>
                    <li class="compare-rows">{{sl.studyformname?sl.studyformname:'——'}}</li>
                    <li class="compare-rows">{{sl.founding?sl.founding:'——'}}</li>
                    <li class="compare-rows">{{sl.enrollage?sl.enrollage:'——'}}</li>
                    <li class="compare-rows">{{sl.fee?sl.fee:'——'}}</li>
                    <li class="compare-rows">{{sl.academicrank?sl.academicrank:'——'}}</li>
                    <li class="compare-rows">{{sl.teacherstudentratio?sl.teacherstudentratio:'——'}}</li>
                    <li class="compare-rows">{{sl.studentsnum?sl.studentsnum:'——'}}</li>
                    <li class="compare-rows">{{sl.intlratio?sl.intlratio:'——'}}</li>
                    <li class="compare-rows">{{sl.addr?sl.addr:'——'}}</li>
                    <li class="compare-rows">{{sl.area?sl.area:'——'}}</li>
                    <li class="compare-rows"><a :href="sl.link">{{sl.link}}</a></li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            schoolList: []
        }
    },
    mounted(){
        if(sessionStorage.getItem('compareList') !== 'undefined'){
            this.schoolList = JSON.parse(sessionStorage.getItem('compareList'))
        }
    },
    methods: {
        cancelCompare(o){
            let arr = this.schoolList
            if(arr.length > 0){
                o.isCompare = false
                for(let j=0; j<arr.length; j++){
                    if(arr[j] == o){
                        arr.splice(j, 1)
                    }
                }
            }
            let scs = JSON.stringify(this.compareItems)
            sessionStorage.setItem('compareList', scs)
        }
    }
}
</script>

<style>

</style>



