<template>
    <div class="success-case-wrapper">
        <div class="banner"></div>
        <div class="slider-wrapper">
            <v-carousel v-model="value" :height="400" dots="none" :autoplay="false" :autoplaySpeed="6000">
                <v-carousel-item v-for="c in demoCase" :key="c.id">
                    <div class="slider-item">
                        <div class="photo">
                            <img :src="'http://47.52.167.136:1328' + c.casepic" alt="">
                        </div>
                        <div class="student-info">
                            <div class="name">{{c.name}}</div>
                            <div class="school">
                                <span class="item-title">录取院校：</span>
                                <span class="item-result">{{c.school}}</span>
                            </div>
                            <div class="age">
                                <span class="item-title">申请年纪：</span>
                                <span class="item-result">{{c.age}}</span>
                            </div>
                            <div class="thoughts">
                                <div class="item-title">相关感言：</div>
                                <p>{{c.speech}}</p>
                            </div>
                        </div>
                    </div>
                </v-carousel-item>
            </v-carousel>
        </div>
        <div class="success-case-panel">
            <div class="common-panel-title">
                <span class="cn">成功案例</span>
                <span class="en">successful cases</span>
            </div>
            <ul class="case-list">
                <li class="case-items" v-for="cl in caseList" :key="cl.id">
                    <div class="case-show">
                        <img :src="'http://47.52.167.136:1328' + cl.casepic" alt="">
                    </div>
                    <div class="case-info">
                        <div class="info-top">{{cl.casecn}}</div>
                        <div class="info-bottom">
                            <span class="location">{{cl.address}}</span>
                            <span class="school">就读于{{cl.school}}</span>
                        </div>
                    </div>
                </li>
            </ul>
            <div class="pagination-wrapper">
                <v-pagination show-quick-jumper :value="casePage" :total="caseTotal" :pageSize="12" @change="getMoreCase"></v-pagination>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            value: 0,
            demoCase: [
                {
                    photo: require('../static/success-case/photo1.png'),
                    name: 'Elane',
                    school: '威雅公学',
                    age: 'YEAR 7',
                    thoughts: '今年11岁，性格沉稳、爱好画画、唱歌和运动。孩子英语基础好，尤其是口语。ES为其在国庆订制了访校。孩子被Godstowe这所传统的女子寄宿学校深深的吸引，也给学校的老师留下了深刻的印象。 随后，通过ES老师积极与学校沟通，为孩子争取到了前往校面试并试课一天的机会。Godstowe是英国第一所女子预备校，由于是non-selective每年都有很多学生申请，名额十分有限。在孩子前往学校面试前，ES英国办的老师专门为孩子安排了模拟面试，这对她最终获得学校offer起到了很大的帮助作用。'
                },
                {
                    photo: require('../static/success-case/photo1.png'),
                    name: 'Elane1',
                    school: '威雅公学',
                    age: 'YEAR 7',
                    thoughts: '今年11岁，性格沉稳、爱好画画、唱歌和运动。孩子英语基础好，尤其是口语。ES为其在国庆订制了访校。孩子被Godstowe这所传统的女子寄宿学校深深的吸引，也给学校的老师留下了深刻的印象。 随后，通过ES老师积极与学校沟通，为孩子争取到了前往校面试并试课一天的机会。Godstowe是英国第一所女子预备校，由于是non-selective每年都有很多学生申请，名额十分有限。在孩子前往学校面试前，ES英国办的老师专门为孩子安排了模拟面试，这对她最终获得学校offer起到了很大的帮助作用。'
                },
                {
                    photo: require('../static/success-case/photo1.png'),
                    name: 'Elan2',
                    school: '威雅公学',
                    age: 'YEAR 7',
                    thoughts: '今年11岁，性格沉稳、爱好画画、唱歌和运动。孩子英语基础好，尤其是口语。ES为其在国庆订制了访校。孩子被Godstowe这所传统的女子寄宿学校深深的吸引，也给学校的老师留下了深刻的印象。 随后，通过ES老师积极与学校沟通，为孩子争取到了前往校面试并试课一天的机会。Godstowe是英国第一所女子预备校，由于是non-selective每年都有很多学生申请，名额十分有限。在孩子前往学校面试前，ES英国办的老师专门为孩子安排了模拟面试，这对她最终获得学校offer起到了很大的帮助作用。'
                },
                {
                    photo: require('../static/success-case/photo1.png'),
                    name: 'Elan3',
                    school: '威雅公学',
                    age: 'YEAR 7',
                    thoughts: '今年11岁，性格沉稳、爱好画画、唱歌和运动。孩子英语基础好，尤其是口语。ES为其在国庆订制了访校。孩子被Godstowe这所传统的女子寄宿学校深深的吸引，也给学校的老师留下了深刻的印象。 随后，通过ES老师积极与学校沟通，为孩子争取到了前往校面试并试课一天的机会。Godstowe是英国第一所女子预备校，由于是non-selective每年都有很多学生申请，名额十分有限。在孩子前往学校面试前，ES英国办的老师专门为孩子安排了模拟面试，这对她最终获得学校offer起到了很大的帮助作用。'
                }
            ],
            caseList: [],
            caseTotal: 0,
            casePage: 1
        }
    },
    created(){
        let that = this

        // 获取推荐案例
        this.axios.get(this.HOST + '/caseRecommend/list?limit=10&offset=0').then((response) => {
            console.log(response.data)
            this.demoCase = response.data.rows
        }).catch((error) => {
            console.log(error)
        })

        this.getCase(0)
    },
    methods: {
        getCase(idx){
            let that = this

            // 获取所有案例列表
            this.axios.get(this.HOST + '/caseOrdinary/list?limit=12&offset='+idx).then((response) => {
                console.log(response.data)
                that.caseTotal = response.data.total
                that.caseList = response.data.rows
            }).catch((error) => {
                console.log(error)
            })
        },
        getMoreCase(i){
            i = i-1
            this.getCase(i)
        }
    }
}
</script>

<style>

</style>



